import React, { useContext } from 'react'
import styles from './Navbar.module.css'
import { UserContext, UserDispatchContext } from '../../store/UserContext'

export default function Navbar() {
  const dispatch = useContext(UserDispatchContext)
  const user = useContext(UserContext)
  function handleLogout() {
    dispatch({
      type: 'logout'
    })
  }
  return (
    <div className={styles.navPanel}>
      <div className={styles.photoBlock}>
        <div className={styles.photo}></div>
        <a className= {styles.userName}> </a>
      </div>
      <div className= {styles.contentBlock}>
        <a className = {styles.navLink} href='/schedule'> </a>
        <a className = {styles.navLink} href='/choreographer'> </a>
        <a className = {styles.navLink} href='/student'> </a>
        <a className = {styles.navLink} href='/lk'>  </a>
      </div>
      <div className= {styles.LogBlock}>
      {!user
        ?
        <a className = {styles.LogItem} href='/login'></a>
        : <> 
          <a className = {styles.LogItem} href='/login' onClick={() => handleLogout() }></a>
        </>
      }
      </div>
    </div>
  )
}
